import React, { useState } from 'react'





export default function Textform(props) {


    const HandleUpClick = () => {
        let newText = text.toUpperCase();
        setText(newText)
    }
    const HandleLoClick = () => {
        let newText = text.toLowerCase();
        setText(newText)
    }

    
    const capitalize = () => {
        
        let firstchar = text.charAt(0); // storing the first char of the string
        let newText= firstchar.toUpperCase(); // converting that to uppercase
        setText(newText+text.slice(1)); // printing it with rest excluding the first char by using slice

    }
    const HandleOnChange = (event) => {
        setText(event.target.value)
    }

    const [text, setText] = useState("");
    return (
        <div>
            <div className=" mb-3">
                <strong><i><h1 className={`text-${props.mode ==='light'?'black':'white'}`}>Enter the text here</h1></i></strong>
                <textarea className={`form-control my-3 text-${props.mode ==='light'?'black':'white'}`}  value={text} onChange={HandleOnChange}
                style = {{backgroundColor: props.mode ==='light'?'white':'grey'}} id="mybox" rows="8" ></textarea>
                
                <button type="button" onClick={HandleUpClick}   className="btn btn-outline-primary mx-1">Convert to Uppercase</button>
                
                <button type="button" onClick={HandleLoClick}   className="btn btn-outline-primary mx-1 ">Convert to Lowercase</button>
                
                <button type="button" onClick={capitalize}   className="btn btn-outline-primary mx-1 ">Convert 
                to Capitalize</button>
            </div>
            
            <div className={`container my-3 text-${props.mode ==='light'?'black':'white'}`}>
                <p>{text === " "  || text === ""? "0" : text.split(" ").length} Words and {text.length} Characters</p>
                <p>
                    {text === " " || text === "" ? "0.000" : 0.008*text.split(" ").length} Minutes to Read
                </p>
            </div>
            <div>
            <div className={`container my-3 text-${props.mode ==='light'?'black':'white'}`}>
                <strong><i><h2 >Preview</h2></i></strong>
                <p>{text===""?"Nothing To Preview": text}</p>
            </div>
            </div>
            
        </div>

    )
}
