import { useState } from 'react';
import './App.css';
import Navbar from './components/Navbar';
import Textform from './components/Textform';
function App() {
  const [mode, setMode] = useState('light')

  const darkMode = () => {

    if (mode === 'light') {
      setMode('dark')
      document.body.style.backgroundColor = 'rgb(71 73 101)';
    }
    else {
      setMode('light')
      document.body.style.backgroundColor = 'white';
    }
  }
  

return (

  <>
    <Navbar title="Textiles" Home="Home" about="About" mode={mode} darkMode={darkMode}/>

    <div className=" container mb-3">
      <Textform mode={mode}/>
    </div>


  </>
);
}

export default App;
